<template>

  <div v-show="typeShop === 'Shop'" >
    <Shop ref="shop" />
  </div>
  <div v-show="typeShop === 'Masks'">
    <Masks ref="masks" />
  </div>
  <div v-show="typeShop === 'Barber'">
    <Barber ref="barber" />
  </div>
  <div v-show="typeShop === 'Tatoo'">
    <Tatoo ref="tatoo" />
  </div>
</template>

<script>

  import Shop from "./components/Shop";
  import Masks from "./components/Masks";
  import Barber from "./components/Barber";
  import Tatoo from "./components/Tatoo";

  export default {
    name: "Shops",
    components: {Shop, Masks, Barber, Tatoo},
    data() {
      return {
        typeShop: ''
      }
    },
    mounted() {
      document.addEventListener("keydown", (e) => {
        if (e.keyCode === 69) {
          window.mp.trigger('cefS::shops:exit')
        }
      })
    }
  }

</script>

