<template>

  <div v-if="!activeWindow" class="loader">
    <div class="loader-title">
      <h1>Добро пожаловать</h1>
      <p>Информация загружается</p>
    </div>
    <div class="loader-gif">
      <img src="../../../assets/img/loader.gif">
    </div>
  </div>
  <div class="block">
    <div class="block-container">
      <div class="container-title">
        <div class="title-exit">
          <div class="exit-btn">
            <p>E</p>
          </div>
          <div class="exit-text">
            <p>Чтобы выйти</p>
          </div>
        </div>
        <div class="title-mouse">
          <img src="../../../assets/img/Shops/mouse.png">
          <p>ЛКМ - Осмотреть</p>
        </div>
      </div>
      <div v-if="active" class="container-content">
        <div class="content-discr" v-if="active.name">
          <div class="discr-title">
            <h1>{{active.name}}</h1>
          </div>
          <div class="discr-content">
            <div class="content-object">
              <div class="object-child">
                <div class="object-title">
                  <p>Стиль маски</p>
                </div>
                <div class="choice-object">
                  <div class="choice-btn"
                       @click="styleNum === 0 ? styleNum = active.style.length-1 : styleNum -= 1">
                    <p>&lt;</p>
                  </div>
                  <div class="choice-input input">
                    <p>{{active.style[styleNum]}}</p>
                  </div>
                  <div class="choice-btn"
                       @click="styleNum === active.style.length-1 ? styleNum = 0 : styleNum += 1">
                    <p>></p>
                  </div>
                </div>
              </div>
            </div>
            <div class="content-price">
              <p>Цена</p>
              <p class="price">${{active.price}}</p>
            </div>
            <div class="content-btns">
              <div class="btns-btn"><p>Оплатить картой</p></div>
              <div class="btns-btn" style="background: linear-gradient(90deg, #FFFFFF 0%, #FFFFFF 100%);"><p>Оплатить наличными</p></div>
            </div>
          </div>
        </div>
        <div class="content-list masks">
          <div class="list-title">
            <h1>{{name}}</h1>
          </div>
          <div class="list-content">
            <div class="content-item"
                 v-for="obj in data"
                 @click="active = obj; styleNum = 0"
                 :key="obj.name" :class="{active: active.name === obj.name}">
              <p>Маска "{{obj.name}}"</p>
              <p class="masks">${{obj.price}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Masks",
  data() {
    return {
      activeWindow: false,
      name: 'Магазин масок №3',
      data: [
        {
          name: 'Маска 1',
          price: 5000,
          style: [
              'Стиль 1', 'Стиль 2'
          ]
        },
        {
          name: 'Маска 2',
          price: 50000,
          style: [
            'Стиль 1', 'Стиль 2'
          ]
        },
        {
          name: 'Маска 3',
          price: 5000,
          style: [
            'Стиль 1', 'Стиль 2'
          ]
        },
      ],
      active: {
        name: 'Маска 2',
        price: 50000,
        style: [
          'Стиль 1', 'Стиль 2'
        ]
      },
      styleNum: 0
    }
  },
  methods: {
    setInfo(data, name) {
      this.objects = data
      this.name = name
      this.activeMenu = this.data[0]
      this.activeWindow = true
    },
    changePreset() {
      window.mp.trigger('cefS::masks:changePreset', JSON.stringify({
        item: this.active.name,
        style: this.active.style[this.styleNum]
      }))
    },
    buyShop(pay) {
      window.mp.trigger('cefS::masks:buyPreset', JSON.stringify({
        item: this.active.name,
        style: this.active.style[this.styleNum],
        price: this.active.price,
        type: pay
      }))
    }
  },
  mounted() {

  }
}
</script>

<style src="../../../assets/scss/Shops/Shop.scss" lang="scss" scoped>

</style>