<template>
  <div class="call">
    <div class="call-title _title">
      <h1>Вызовы</h1>
    </div>
    <div class="call-info">
      <div class="info-title" >
        <div class="info-time" >
          <p v-if="!active">Вызов...</p>
          <p v-else>Разговор... / {{timeCall.minutes}}:<span v-if="timeCall.seconds.toString().length === 1">0</span>{{timeCall.seconds}}</p>
        </div>
        <p class="info-user">{{dataCallToUser.active ? dataCallToUser.name : dataCallFromUser.name}}</p>
        <p class="info-number"> {{dataCallToUser.active ? dataCallToUser.number : dataCallFromUser.number}}</p>
      </div>
      <div class="info-circle">
        <img src="../../../assets/img/Phone/user.png">
      </div>
    </div>
    <div class="call-remove _flex">
      <div class="remove-btn"
           @click="callRemove">
        <img src="../../../assets/img/Phone/removeCall.png">
      </div>
      <div v-if="dataCallToUser.active && !active && timeCall.seconds === 0 && timeCall.minutes === 0" class="remove-btn" @click="acceptCallToUser()">
        <img src="../../../assets/img/Phone/removeCall.png">
      </div>
    </div>
    <div class="call-end" v-if="!active && (timeCall.minutes !== 0 || timeCall.seconds !== 0)">
      <h1>Разговор закончен.</h1>
      <p>{{timeCall.minutes}}:{{timeCall.seconds.toString().length === 1 ? '0' + timeCall.seconds : timeCall.seconds}}</p>
    </div>
  </div>

</template>

<script>
export default {
  name: "Call",
  props: {
    dataCallFromUser: Object,
    dataCallToUser: {
      // eslint-disable-next-line vue/require-valid-default-prop
      default: 1,
      type: Object
    }
  },
  data() {
    return {
      timeCall: {
        minutes: 0,
        seconds: 0
      },
      active: false,
      data: {
        name: 'John Dean',
        number: 123456
      },
      activeTimer: ''
    }
  },
  methods: {
    activateCall() {
      this.active = true

      this.activeTimer = setInterval(() => {
        this.timeCall.seconds += 1
        if(this.timeCall.seconds === 60) {
          this.timeCall.minutes += 1
          this.timeCall.seconds = 0
        }
      }, 1000)
    },
    callRemove() {
      clearInterval(this.activeTimer)
      this.active = false
      if(this.timeCall.minutes === 0 && this.timeCall.seconds === 0) {
        /*if(this.dataCallToUser) {
          window.mp.trigger('cefS::phone:cancelCallToUser', JSON.stringify(this.dataCallToUser))
        } else {
          window.mp.trigger('cefS::phone:removeCallFromUser', JSON.stringify(this.dataCallFromUser))
        }*/
        this.$emit('removeCall')
      } else {
        setTimeout(() => {
          this.$emit('removeCall')
        }, 2500)
      }
    },
    acceptCallToUser() {
      this.active = true
      this.activateCall()
      //window.mp.trigger('cefS::phone:acceptCallToUser', JSON.stringify(this.dataCallToUser))
    }
  },
  mounted() {

  }
}
</script>

<style lang="scss" src="../../../assets/scss/Phone/Call.scss" scoped>

</style>