<template>
  <div class="content">
    <div class="content-card" :class="{just: !characterData}">
      <div class="card-title"
           :class="{first: num === 0, two: num === 1}">
        <div class="title-div">
          <h1>{{characterData ? characterData.name : 'Свободный слот'}}</h1>
        </div>
        <div class="touch-title">
          <img src="../../../assets/img/ChoiseCharacter/just.png" alt="">
        </div>
      </div>
      <div class="card-info" v-if="characterData">
        <div class="card-ban" v-if="characterData.banActive">
          <div class="ban-container">
            <div class="ban-icon">
              <img src="../../../assets/img/ChoiseCharacter/banActive.png">
            </div>
            <div class="ban-active">
              <div class="ban-title">
                <h1>Ban</h1>
              </div>
              <div class="ban-info">
                <div class="ban-reason">
                  <h1>{{characterData.banReason}}</h1>
                </div>
                <div class="ban-dates">
                  <div class="ban-date">
                    <p class="date">{{characterData.dateBan}}</p>
                    <p>Дата бана</p>
                  </div>
                  <div class="ban-date">
                    <p class="date">{{characterData.dateRazBan}}</p>
                    <p>Дата разбана</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="info-child">
          <p class="child-title">Уровень</p>
          <p>{{characterData.lvl}}</p>
        </div>
        <div class="info-child">
          <p class="child-title">EXP</p>
          <p>{{characterData.exp}}</p>
        </div>
        <div class="info-child">
          <p class="child-title">Фракция</p>
          <p>{{characterData.fraction ? characterData.fraction : 'Отсутствует'}}</p>
        </div>
        <div class="info-child">
          <p class="child-title">Наличные</p>
          <p>${{characterData.cash}}</p>
        </div>
        <div class="info-child">
          <p class="child-title">Банк</p>
          <p>${{characterData.bank}}</p>
        </div>
      </div>
      <div class="card-empty" v-else-if="num != 2">
        <p>Слушай, парень.</p>
        <p>Этот слот реально свободен.</p>
        <p>Используй его с умом.</p>
      </div>
      <div class="card-vip" v-else>
        <p class="vip-title">Если тебе понадобится этот слот, то ты знаешь во сколько он тебе обойдется.</p>
        <p class="vip-buy">ЦЕНА СЛОТА - 1000Vcoins</p>
        <p class="vip-bonus">+Бонус: VipGold на 1 месяц</p>
      </div>
      <div class="card-btns" v-if="!characterData || !characterData.banActive">
        <div class="btns" v-if="characterData">
          <div v-if="!characterData.banActive" class="btns-btn__go" @click="choise(characterData.name)">
            <p>Выбрать персонажа</p>
          </div>
          <div class="btns-btn__del" @click="dellCharacter(characterData.name)">
            <p>Удалить персонажа</p>
          </div>
        </div>
        <div class="btns-empty" v-else-if="num != 2">
          <div class="btns-btn__go" @click="createCharacter">
            <p>Создать персонажа</p>
          </div>
        </div>
        <div class="btns-vip" v-else>
          <div class="btns-btn__go" @click="activeQuestion = true">
            <p>Разблокировать</p>
          </div>
        </div>
      </div>
      <div class="touch-title down">
        <img src="../../../assets/img/ChoiseCharacter/just.png" alt="">
      </div>
    </div>
  </div>
  <div v-if="activeQuestion" class="unlock-question">
    <div class="question-title">
      <div class="title-name">
        <p>Тревор</p>
      </div>
      <div class="title-text">
        <p>Слышь, не торопись.</p>
      </div>
    </div>
    <div class="question-text">
      <p>Уверен, что хочешь его?</p>
    </div>
    <div class="question-btns">
      <div class="btn success" @click="unlockVip">
        <p>Да, погнали</p>
      </div>
      <div class="btn" @click="activeQuestion = false">
        <p>Нет, подумаю</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'cardUserChoise',
  props: {
    characterData: Object,
    num: Number,
    donate: Number
  },
  data() {
    return {
      activeQuestion: false
    }
  },
  methods: {
    choise(name) {
      this.$emit('choiceCharacter', name)
    },
    dellCharacter(name) {
      window.mp.trigger('cefS::dellCharacter', name)
    },
    createCharacter() {
      this.$emit('startCreateCharacter')
    },
    unlockVip() {
      window.mp.trigger('cefS::unlockVipSlot')
    }
  },
}
</script>

<style src="../../../assets/scss/choiseCharacter/choiseCard.scss" lang="scss" scoped>



</style>