<template>

  <div class="number">
    <div class="number-title _title">
      <h1>Вызовы</h1>
    </div>

    <div class="number-input">
      <p>{{number}}</p>
    </div>

    <div class="number-btn">
      <div class="btn-add"
           @click="number ? $emit('newContact', number) : null">
        <p>Добавить контакт</p>
      </div>

      <div class="btn-list">
        <div class="list-item"
             :key="el"
             v-for="el in btns" @click="number.length <= 12 ? number = number + el : ''">
          <h1>{{el}}</h1>
        </div>
      </div>
    </div>

    <div class="number-call" @click="callUser">
      <img src="../../../assets/img/Phone/Call.png">
    </div>
  </div>

</template>

<script>
export default {
  name: "setNumber",
  data() {
    return {
      btns: [
          1,2,3,4,5,6,7,8,9,0
      ],
      number: ''
    }
  },
  methods: {
    callUser() {
      if(this.number.length > 4) {
        this.$emit('callNumberUser', {
          name: 'Неизвестный',
          number: this.number
        })
      }
    }
  },
  mounted() {
    document.addEventListener("keydown", ev => {
      if(this.number.length >= 12 && ev.keyCode !== 8) return;
      switch (ev.keyCode) {
        case 8: {
          this.number = this.number.substr(0, this.number.length-1)
          break
        }
        case 48: {
          this.number = this.number + 0
          break
        }
        case 49: {
          this.number = this.number + 1
          break
        }
        case 50: {
          this.number = this.number + 2
          break
        }
        case 51: {
          this.number = this.number + 3
          break
        }
        case 52: {
          this.number = this.number + 4
          break
        }
        case 53: {
          this.number = this.number + 5
          break
        }
        case 54: {
          this.number = this.number + 6
          break
        }
        case 55: {
          this.number = this.number + 7
          break
        }
        case 56: {
          this.number = this.number + 8
          break
        }
        case 57: {
          this.number = this.number + 9
          break
        }
        case 96: {
          this.number = this.number + 0
          break
        }
        case 97: {
          this.number = this.number + 1
          break
        }
        case 98: {
          this.number = this.number + 2
          break
        }
        case 99: {
          this.number = this.number + 3
          break
        }
        case 100: {
          this.number = this.number + 4
          break
        }
        case 101: {
          this.number = this.number + 5
          break
        }
        case 102: {
          this.number = this.number + 6
          break
        }
        case 103: {
          this.number = this.number + 7
          break
        }
        case 104: {
          this.number = this.number + 8
          break
        }
        case 105: {
          this.number = this.number + 9
          break
        }
        default: {
          return
        }
      }
    })
  }
}
</script>

<style lang="scss" src="../../../assets/scss/Phone/setNumber.scss" scoped>

</style>