<template>

  <div class="block">
    <div class="block-content">
      <div class="content-message">
        <div class="message-icon">
          <img src="../../../assets/img/ChoiseCharacter/DonateSlot/info.png" alt="">
        </div>
        <div class="message-text">
          <p>С возвращением, дорогуша</p>
          <p>Я УЖЕ СТАЛ ДУМАТЬ, ЧТО ТВОЙ ТОЩИЙ ЗАД ПОДСТРЕЛИЛИ.</p>
          <div class="message-name">
            <p>Тревор</p>
          </div>
        </div>
      </div>
      <div class="content-choice">
        <div class="choice-card">
          <div class="card-text">
            <div class="text-info">
              <h1>Место выхода</h1>
            </div>
            <div class="text-notif">
              <p>Вы ничего не потеряете</p>
            </div>
          </div>
          <div class="card-btn">
            <div @click="choiceSpawn('Last')" class="btn">
              <p>Заспавниться</p>
            </div>
          </div>
        </div>
        <div class="choice-card fraction">
          <div class="card-text">
            <div class="text-info">
              <h1>Территория фракции</h1>
            </div>
            <div class="text-notif" :class="{emptyBlock: !data.fraction}">
              <p>{{data.fraction ? 'Вы ничего не потеряете' : 'Вы не состоите во фракции'}}</p>
            </div>
          </div>
          <div class="card-btn">
            <div class="btn"
                 :class="{blockContent: !data.fraction}"
                 @click="data.fraction ? choiceSpawn('Fraction') : ''"
            >
              <p>{{data.fraction ? 'Заспавниться' : 'Недоступно'}}</p>
            </div>
          </div>
        </div>
        <div class="choice-card home">
          <div class="card-text">
            <div class="text-info">
              <h1>Дом</h1>
            </div>
            <div class="text-notif emptyBlock"
                 :class="{emptyBlock: !data.homeActive}"
            >
              <p>{{data.homeActive ? 'Вы ничего не потеряете' : 'У вас нет дома'}}</p>
            </div>
          </div>
          <div class="card-btn">
            <div class="btn"
                 :class="{blockContent: !data.homeActive}"
                 @click="data.homeActive ? choiceSpawn('Home') : ''"
            >
              <p>{{data.homeActive ? 'Заспавниться' : 'Недоступно'}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  name: "choiceSpawn",
  props: {
    data: Object
  },
  methods: {
    choiceSpawn(spawn) {
      let data = {
        name: this.data.name,
        spawn: spawn
      }
      window.mp.trigger('cefS::auth:startGame', JSON.stringify(data))
    }
  },
}
</script>

<style lang="scss" src="../../../assets/scss/choiseCharacter/choiceSpawn.scss" scoped>

</style>