<template>

  <div class="block">
    <div class="container">
      <div class="block-content">
        <div class="content-exit">
          <div class="exit-btn">
            <p>E</p>
          </div>
          <div class="exit-text">
            <p>Чтобы выйти</p>
          </div>
        </div>
        <div class="content-discr" v-if="active && activeCar.data.property">
          <div class="discr-title">
            <h1>Характеристики</h1>
          </div>
          <div class="discr-prop">
            <div class="prop-container">
              <div class="prop-child">
                <div class="child-title">
                  <p>Скорость</p>
                </div>
                <div class="child-blocks">
                  <div class="blocks-child" v-if="activeCar.data.property.speed >= 1"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.speed >= 2"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.speed >= 4"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.speed >= 5"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.speed >= 6"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.speed >= 7"></div>
                  <div class="blocks-bg"></div>
                </div>
              </div>
              <div class="child-img">
                <img src="../../assets/img/carDealership/speedometer.png" alt="">
              </div>
            </div>
            <div class="prop-container">
              <div class="prop-child">
                <div class="child-title">
                  <p>Тормоза</p>
                </div>
                <div class="child-blocks">
                  <div class="blocks-child" v-if="activeCar.data.property.brake >= 1"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.brake >= 2"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.brake >= 3"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.brake >= 4"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.brake >= 5"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.brake >= 6"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.brake >= 7"></div>
                  <div class="blocks-bg"></div>
                </div>
              </div>
              <div class="child-img">
                <img src="../../assets/img/carDealership/brake.png" alt="">
              </div>
            </div>
            <div class="prop-container">
              <div class="prop-child">
                <div class="child-title">
                  <p>Сцепление</p>
                </div>
                <div class="child-blocks">
                  <div class="blocks-child" v-if="activeCar.data.property.clutch >= 1"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.clutch >= 2"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.clutch >= 3"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.clutch >= 4"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.clutch >= 5"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.clutch >= 6"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.clutch >= 7"></div>
                  <div class="blocks-bg"></div>
                </div>
              </div>
              <div class="child-img">
                <img src="../../assets/img/carDealership/clutch.png" alt="">
              </div>
            </div>
            <div class="prop-container">
              <div class="prop-child">
                <div class="child-title">
                  <p>Ускорение</p>
                </div>
                <div class="child-blocks">
                  <div class="blocks-child" v-if="activeCar.data.property.acceleration >= 1"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.acceleration >= 2"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.acceleration >= 3"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.acceleration >= 4"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.acceleration >= 5"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.acceleration >= 6"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.acceleration >= 7"></div>
                  <div class="blocks-bg"></div>
                </div>
              </div>
              <div class="child-img">
                <img src="../../assets/img/carDealership/acceleration.png" alt="">
              </div>
            </div>
            <div class="prop-container">
              <div class="prop-child">
                <div class="child-title">
                  <p>Расход</p>
                </div>
                <div class="child-blocks">
                  <div class="blocks-child" v-if="activeCar.data.property.fuel >= 1"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.fuel >= 2"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.fuel >= 3"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.fuel >= 4"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.fuel >= 5"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.fuel >= 6"></div>
                  <div class="blocks-child" v-if="activeCar.data.property.fuel >= 7"></div>
                  <div class="blocks-bg"></div>
                </div>
              </div>
              <div class="child-img">
                <img src="../../assets/img/carDealership/fuel.png" alt="">
              </div>
            </div>
          </div>
        </div>
        <div class="content-colors">
          <div class="colors-child"
               id="colorBlock"
               @click="changeColor(color)"
               v-for="color in colors" :style="{background: color, height: heightColor + 'px'}" :key="color">
          </div>
        </div>
        <div class="content-list">
          <div class="list-title">
            <h1>{{nameBiz}}</h1>
          </div>
          <div class="list-content">
            <div class="content-sort">
              <p>Сортировать по:</p>
              <div class="sort-now"
                   :class="{active: sort === 'price', up: reverse === 1}"
                   @click="changeSort('price')">
                <p>Цене</p>
              </div>
              <div class="sort-now big"
                   :class="{active: sort != 'price', up: reverse === 1}"
                   @click="changeSort('name')">
                <p>Наименованию</p>
              </div>
            </div>
            <div class="content-items">
              <div class="items-item"
                   v-for="(car, index) in sort === 'price' ?
                   ( reverse === 0 ? data.sort((prev, next) => prev.price - next.price) : data.sort((prev, next) => next.price - prev.price))
                   : (reverse === 0 ? data.sort((prev, next) => {
                    if ( prev.name < next.name ) return -1;
                    if ( prev.name < next.name ) return 1;
                    }) : data.sort((prev, next) => {
                    if ( next.name < prev.name ) return -1;
                    if ( next.name < prev.name ) return 1;
                    }))"
                   :key="index" @click="changeCar(car, index)"
                   :class="{active: activeCar.index === index}">
                <div class="item-discr">
                  <div class="discr-title">
                    <p>{{index+1}}. {{car.name}}</p>
                  </div>
                  <div class="discr-price">
                    <p>${{car.price}}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="content-dops">
          <div class="dops-price">
            <h1>$ {{activeCar.data.price}}</h1>
          </div>
          <div @click="buyCar" class="dops-buy">
            <h1>Купить</h1>
          </div>
          <div @click="testDrive" class="dops-test">
            <p>Тест драйв</p>
          </div>
        </div>
      </div>
    </div>
  </div>
  <loader v-if="!active" />

</template>

<script>

import loader from "../loader";

export default {
  name: "carDealership",
  components: {loader},
  data() {
    return {
      active: false,
      data: [
        {
          name: 'Машина 1',
          modelName: '',
          price: 2000000,
          property: {
            speed: 7,
            brake: 2,
            clutch: 5,
            acceleration: 1,
            fuel: 7
          }
        },
        {
          name: 'Машина 12',
          modelName: '',
          price: 200000,
          property: {
            speed: 2,
            brake: 5,
            clutch: 2,
            acceleration: 2,
            fuel: 4
          }
        }
      ],
      colors: [
        '#FF24E9',
        '#FF34C6',
        '#FF3465',
        '#986D00',
        '#864000',
        '#FF24E9',
        '#FF34C6',
        '#FF3465',
        '#986D00',
        '#864000',
        '#FF24E9',
        '#FF34C6',
        '#FF3465',
        '#986D00',
        '#864000'
      ],
      activeCar: {
        index: 0,
        data: Object
      },
      sort: 'price',
      reverse: 0,
      heightColor: 20,
      nameBiz: 'Автосалон №1'
    }
  },
  methods: {
    changeCar(car, index) {
      this.activeCar.index = index
      this.activeCar.data = car
    },
    setupInfo(data, name) {

      this.data = data
      this.nameBiz = name

      this.activeCar.index = 0
      this.activeCar.data = this.data[0]

      this.active = true
    },
    buyCar() {
      window.mp.trigger('cefS::carDealership:buyCar', JSON.stringify(this.activeCar.data))
    },
    testDrive() {
      window.mp.trigger('cefS::carDealership:testDrive', JSON.stringify(this.activeCar.data))
    },
    changeColor(hex) {
      window.mp.trigger('cefS::carDealership:changeColor', hex)
    },
    changeSort(sort) {
      const remote = this.sort === sort ? true : false
      this.sort = sort
      if(sort === 'price') {

        if(remote && this.reverse === 0) {
          this.data.sort((prev, next) => next.price - prev.price)
          this.reverse = 1
        } else {
          this.reverse = 0
          this.data.sort((prev, next) => prev.price - next.price)
        }
        this.activeCar.data = this.data[0]
        this.activeCar.index = 0

      } else {

        if(remote && this.reverse === 0) {
          this.data.sort((prev, next) => {
            if ( next.name < prev.name ) return -1;
            if ( next.name < prev.name ) return 1;
          })
          this.reverse = 1
        } else {
          this.reverse = 0
          this.data.sort((prev, next) => {
            if ( prev.name < next.name ) return -1;
            if ( prev.name < next.name ) return 1;
          })
        }
        this.activeCar.data = this.data[0]
        this.activeCar.index = 0
      }
    }
  },
  mounted() {

    window.addEventListener("load", () => {
      this.heightColor = document.querySelector('.colors-child').offsetWidth
    })

    document.addEventListener("keydown", (e) => {
      if (e.keyCode === 69) {
        window.mp.trigger('cefS::carDealership:closeWindow')
      }
    })

  }
}
</script>

<style lang="scss" src="../../assets/scss/carDealership/carDealership.scss" scoped>

</style>