<template>

  <div class="parking _flex" style="overflow:hidden; flex-direction: column; width: 100%; height: 100%;">
    <div class="parking-title _title">
      <h1>Парковка</h1>
    </div>
    <div class="parking-list _flex">
      <div class="list-item _flex"
           :class="{activeList: activeCar.active, thisActive: activeCar.active && activeCar.num === i}"
           @click="activeCar.active && activeCar.num !== i ? '' :
            activeCar.active && activeCar.num === i ?
           (activeCar.active = false, activeCar.num = Number) :
           (activeCar.active = true, activeCar.num = i)"
           v-for="(el, i) in dataCars"
           :key="i">
        <div class="item-title">
          <p>{{el.name.length > 15 ? el.name.substr(0, 13) + '...' : el.name}}</p>
        </div>
        <div class="item-img">
          <img src="../../../assets/img/Phone/car.png">
        </div>
      </div>
    </div>
    <div class="list-menu" v-if="activeCar.active">
      <div @click="changeMenu(0)" class="menu _flex">
        <div class="menu-img">
          <img src="../../../assets/img/Phone/Parking/settings.png">
        </div>
        <div class="menu-title">
          <h1>Восстановить</h1>
        </div>
        <div class="menu-price">
          <p>$100</p>
        </div>
      </div>
      <div @click="changeMenu(1)" class="menu _flex">
        <div class="menu-img">
          <img src="../../../assets/img/Phone/Parking/key.png">
        </div>
        <div class="menu-title">
          <h1>Дубликат ключа</h1>
        </div>
      </div>
      <div @click="changeMenu(2)" class="menu _flex">
        <div class="menu-img">
          <img src="../../../assets/img/Phone/Parking/unlock.png">
        </div>
        <div class="menu-title">
          <h1>Сменить замки</h1>
        </div>
      </div>
      <div @click="changeMenu(3)" class="menu _flex">
        <div class="menu-img">
          <img src="../../../assets/img/Phone/Parking/permit.png">
        </div>
        <div class="menu-title">
          <h1>Эвакуировать машину</h1>
        </div>
      </div>
      <div @click="changeMenu(4)" class="menu _flex">
        <div class="menu-img">
          <img src="../../../assets/img/Phone/Parking/car.png">
        </div>
        <div class="menu-title">
          <h1>Продать</h1>
        </div>
        <div class="menu-price">
          <p>${{dataCars[activeCar.num].price}}</p>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  name: "parking",
  data() {
    return {
      dataCars: [
        {
          name: 'Car 1 YOUR PUSSY SUCK MY DICK',
          price: 100000
        },
        {
          name: 'Car 2',
          price: 10000
        }
      ],
      activeCar: {
        active: false,
        num: Number
      }
    }
  },
  methods: {
    changeMenu(num) {

      const nowCar = this.dataCars[this.activeCar.num]

      switch (num) {
        case 0: {
          window.mp.trigger('cefS::phone:repairCar', JSON.stringify(nowCar))
          break
        }
        case 1: {
          window.mp.trigger('cefS::phone:duplicateKey', JSON.stringify(nowCar))
          break
        }
        case 2: {
          window.mp.trigger('cefS::phone:changeLock', JSON.stringify(nowCar))
          break
        }
        case 3: {
          window.mp.trigger('cefS::phone:evacuateCar', JSON.stringify(nowCar))
          break
        }
        case 4: {
          window.mp.trigger('cefS::phone:sellCar', JSON.stringify(nowCar))
          break
        }
      }

      this.activeCar.active = false
    }
  },
}
</script>

<style lang="scss" src="../../../assets/scss/Phone/parking.scss" scoped>

</style>