            <h1>{{activeMenu.name}}</h1>
          </div>
          <div class="discr-content">
            <div class="content-object">
              <div class="object-child">
                <div class="object-title">
                  <p>Вариация вещи</p>
                </div>
                <div class="choice-object">
                  <div class="choice-btn"
                       @click="objectNum === 0 ?
                       (objectNum = activeMenu.objects.length-1, changePreset) :
                       (objectNum -= 1, changePreset);
                       activeMenu.objects.length > 1 ? styleNum = 0 : ''">
                    <p>&lt;</p>
                  </div>
                  <div class="choice-input input">
                    <p>{{activeMenu.objects[objectNum].name}}</p>
                  </div>
                  <div class="choice-btn"
                       @click="objectNum === activeMenu.objects.length-1 ?
                       (objectNum = 0, changePreset) : (objectNum += 1, changePreset);
                       activeMenu.objects.length > 1 ? styleNum = 0 : '';">
                    <p>></p>
                  </div>
                </div>
              </div>

              <div class="object-child">
                <div class="object-title">
                  <p>Стиль вещи</p>
                </div>
                <div class="choice-object">
                  <div class="choice-btn"
                       @click="changePreset(); styleNum === 0 ?
                       styleNum = activeMenu.objects[objectNum].styles.length-1 :
                       styleNum -= 1">
                    <p>&lt;</p>
                  </div>
                  <div class="choice-object input">
                    <p>{{activeMenu.objects[objectNum].styles[styleNum]}}</p>
                  </div>
                  <div class="choice-btn"
                       @click="changePreset(); styleNum === activeMenu.objects[objectNum].styles.length-1 ?
                       styleNum = 0 : styleNum += 1; ">
                    <p>></p>
                  </div>
                </div>
              </div>
            </div>
            <div class="content-price">
              <p>Цена</p>
              <p class="price">${{activeMenu.objects[objectNum].price}}</p>
            </div>
            <div class="content-btns">
              <div class="btns-btn" @click="buyShop('cash')"><p>Оплатить картой</p></div>
              <div class="btns-btn"
                   @click="buyShop('bank')"
                   style="background: linear-gradient(90deg, #FFFFFF 0%, #FFFFFF 100%);"><p>Оплатить наличными</p></div>
            </div>
          </div>
        </div>
        <div class="content-list">
          <div class="list-title">
            <h1>{{name}}</h1>
          </div>
          <div class="list-content">
            <div class="content-item"
                 v-for="(obj, i) in menu"
                 @click="changeMenu(i)"
