<template>
  <main v-show="url === 'auth' && !activeAuth && !activeCreateCharacter"
        class="auth-section gta-acount  authorization  auth-page  show"
        id="auth-section"
  >
    <div class="switch-flex">
      <div class="auth-section-wrp">
        <div class="auth-form form-wrp">
          <div class="form-text">
            <div class="form-logo">
              <div class="form-logo__img">
                <img src="../../assets/img/auth/logo.png" alt="logo">
              </div>
              <div class="form-logo__text">
                <h2 class="form-h">
                  Авторизация
                </h2>
                <p>
                  С возвращением, дорогой игрок
                </p>
              </div>
            </div>
            <form id="auth-form" action="" method="POST">
              <h3 class="auth-form__title">
                Введите Ваш логин
              </h3>
              <div class="auth-form__input-wrapper">
                <div class="auth-form__icon"
                     :class="{active: activeLogin === 'login'}"
                >
                  <img src="./../../assets/img/auth/user_Icon.png" alt="user" class="auth-form__icon-img">
                </div>
                <input maxlength="20"
                       @focus="activeLogin = 'login'"
                       @focusout="activeLogin = ''"
                       :class="{active: activeLogin === 'login'}"
                       v-model="dataAuth.login"
                       type="text"
                       name="entry-login"
                       class="entry-login login__input input"
                       required>
              </div>
              <h3 class="auth-form__title">
                Введите пароль
              </h3>
              <div class="auth-form__input-wrapper">
                <div class="auth-form__icon"
                     :class="{active: activeLogin === 'pass'}"
                >
                  <img src="./../../assets/img/auth/pass_Icon.png" alt="pass" class="auth-form__icon-img">
                </div>
                <input @focus="activeLogin = 'pass'"
                       @focusout="activeLogin = ''"
                       :class="{active: activeLogin === 'pass'}"
                       v-model="dataAuth.pass"
                       type="password"
                       name="entry-password"
                       id="entry-password-id"
                       class="password__input input"
                       required>
              </div>
              <div class="auth-form__buttons">
                <div class="button__wrapper">
                  <input @click.prevent="goAuth" class="auth__item button btn-enter js-btn-auth button--active" type="button" value="АВТОРИЗОВАТЬСЯ">
                  <div class="auth" @click="goAuth" style="opacity: 0">Войти</div>
                </div>
                <div class="button__wrapper">
                  <a @click="url = 'reg'" class="btn-register js-btn-register create__item">
                    ЗАРЕГИСТРИРОВАТЬСЯ
                  </a>
                </div>
                <a @click="url = 'pass'" class="recovery__item restore-pw js-btn-restore">
                  Забыл пароль
                </a>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </main>
  <main v-show="url === 'reg' && !activeAuth && !activeCreateCharacter"
        class="reg-section gta-acount  registration  reg-page"
        id="reg-section">
    <div class="switch-flex">
      <div class="auth-section-wrp">
        <div class="reg-form form-wrp">
          <div class="form-text">
            <div class="form-logo">
              <div class="form-logo__img">
                <img src="./../../assets/img/auth/logo.png" alt="logo">
              </div>
              <div class="form-logo__text">
                <h2 class="form-h">
                  Регистрация
                </h2>
                <p>
                  Есть аккаунт? Вернитесь назад.
                </p>
              </div>
            </div>
            <form id="reg-form" action="" method="POST">
              <h3 class="reg-form__title">Введите Ваш логин</h3>
              <div class="reg-form__input-wrapper">
                <div class="reg-form__icon"
                     :class="{active: activeReg === 'Login'}"
                >
                  <img src="./../../assets/img/auth/user_Icon.png" alt="user" class="reg-form__icon-img">
                </div>
                <input type="text"
                       maxlength="50"
                       name="new-user__login"
                       id="new-user__login-id"
                       class="name__input input"
                       @focus="activeReg = 'Login'"
                       @focusout="activeReg = ''"
                       :class="{active: activeReg === 'Login'}"
                       v-model="dataReg.login"
                       required>
              </div>
              <h3 class="reg-form__title">Введите Ваш EMAIL</h3>
              <div class="reg-form__input-wrapper">
                <div class="reg-form__icon"
                     :class="{active: activeReg === 'Email'}">
                  <img src="./../../assets/img/auth/mail_Icon.png" alt="mail" class="reg-form__icon-img">
                </div>
                <input type="email"
                       name="new-user__email"
                       class="email__input input"
                       id="new-user__email-id"
                       @focus="activeReg = 'Email'"
                       @focusout="activeReg = ''"
                       :class="{active: activeReg === 'Email'}"
                       v-model="dataReg.email"
                       required>
              </div>
              <h3 class="reg-form__title">Придумайте пароль</h3>
              <div class="reg-form__input-wrapper">
                <div class="reg-form__icon"
                     :class="{active: activeReg === 'Pass'}"
                >
                  <img src="./../../assets/img/auth/pass_Icon.png" alt="pass" class="reg-form__icon-img">
                </div>
                <input type="password"
                       name="new-user__pw"
                       id="new-user__pw-id"
                       class="password__input input"
                       minlength="3"
                       @focus="activeReg = 'Pass'"
                       @focusout="activeReg = ''"
                       :class="{active: activeReg === 'Pass'}"
                       v-model="dataReg.pass"
                       required>
              </div>
              <h3 class="reg-form__title">Повторите пароль</h3>
              <div class="reg-form__input-wrapper">
                <div class="reg-form__icon"
                     :class="{active: activeReg === 'RePass'}">
                  <img src="./../../assets/img/auth/pass_Icon.png" alt="pass" class="reg-form__icon-img">
                </div>
                <input type="password"
                       name="new-user__pw-repeat"
                       minlength="3"
                       id="new-user__pw-repeat-id"
                       class="password__input input"
                       @focus="activeReg = 'RePass'"
                       @focusout="activeReg = ''"
                       :class="{active: activeReg === 'RePass'}"
                       required>
              </div>
              <h3 class="reg-form__title">Промокод</h3>
              <div class="reg-form__input-wrapper">
                <div class="reg-form__icon"
                     :class="{active: activeReg === 'Promo'}">
                  <img src="./../../assets/img/auth/gift_Icon.png" alt="gift" class="reg-form__icon-img">
                </div>
                <input type="text"
                       name="new-user__promo-code"
                       id="new-user__promo-code"
                       class="promo__input input promo-code"
                       @focus="activeReg = 'Promo'"
                       @focusout="activeReg = ''"
                       v-model="dataReg.promo"
                       :class="{active: activeReg === 'Promo'}"
                >
              </div>
              <div class="reg-form__buttons">
                <div class="button__wrapper">
                  <input @click="goReg" class="btn-register-end auth__item button button--active" type="button" value="ЗАРЕГИСТРИРОВАТЬСЯ">
                </div>
                <a @click="url = 'auth'" class="create__item btn-back  js-btn-back">Назад</a>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </main>
  <main v-show="url === 'pass' && !activeAuth && !activeCreateCharacter"
        class="recovery-section gta-acount  restorepass  rest-page"
        id="recovery-section">
    <div class="switch-flex">
      <div class="auth-section-wrp">
        <div class="recovery-form form-wrp">
          <div class="form-text">
            <div class="form-logo">
              <div class="form-logo__img">
                <img src="../../assets/img/auth/logo.png" alt="logo">
              </div>
              <div class="form-logo__text">
                <h2 class="form-h">
                  Восстановление
                </h2>
                <p>
                  Если вы забыли пароль, воссстановите.
                </p>
              </div>
            </div>
            <form id="rest-form" action="" method="POST">
              <h3 class="rest-form__title">
                Введите Ваш логин
              </h3>
              <div class="rest-form__input-wrapper">
                <div class="rest-form__icon"
                     :class="{active: activePass}">
                  <img src="../../assets/img/auth/user_Icon.png" alt="user" class="rest-form__icon-img">
                </div>
                <input type="text"
                       placeholder="Логин"
                       maxlength="50"
                       class="entry-login login__input input"
                       name="entry-login"
                       id="entry-login-id"
                       :class="{active: activePass}"
                       @focus="activePass = true"
                       @focusout="activePass = false"
                       v-model="dataLoginPass"
                       required>
              </div>
              <div class="rest-form__buttons">
                <div class="button__wrapper">
                  <input @click="goRecover" class="btn-restore-end button button--active" type="button" value="Восстановить">
                </div>
                <a @click="url = 'auth'" class="create__item btn-back js-btn-back">
                  Назад
                </a>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </main>
  <choiseCharacter @createCharacter="activeCreateCharacter = true" @goChoiceSpawn="goChoiceSpawn"
                   v-show=" activeAuth && !activeChoiceSpawn && !activeCreateCharacter"
                   :characters="characters.characters"
                   :donate="characters.vcoins" />
  <choiceSpawn v-show="activeChoiceSpawn && !activeCreateCharacter"
               :data="dataChoiceSpawn"/>
  <createCharacter @returnOnCreate="activeCreateCharacter = false" v-show="activeCreateCharacter"/>
</template>

<script>

import choiseCharacter from "./components/choiseCharacter";
import choiceSpawn from "./components/choiceSpawn";
import createCharacter from "./components/createCharacter";

export default {
  name: "windowLogin",
  components: {choiseCharacter,choiceSpawn, createCharacter},
  props: {
    donate: Number
  },
  data() {
    return {
      test: true,
      url: "auth",
      activeLogin: '',
      activeReg: '',
      activePass: false,
      activeAuth: false,
      activeChoiceSpawn: false,
      activeCreateCharacter: false,
      dataAuth: {
        login: '',
        pass: ''
      },
      dataReg: {
        login: '',
        email: '',
        pass: '',
        promo: ''
      },
      dataLoginPass: '',
      dataChoiceSpawn: {
        name: 'Test',
        lvl: 6,
        exp: 12,
        fraction: '',
        cash: 10000,
        bank: 10000,
        homeActive: true
      },
      characters: {
        vcoins: 1000,
        characters: [
          {
            name: 'John Dean',
            lvl: 6,
            exp: 12,
            fraction: '',
            cash: 10000,
            bank: 10000,
            homeActive: true,
            banActive: false,
            banReason: 'Нарушение правил сервера',
            dateBan: '02.04.2021',
            dateRazBan: '12.04.2021'
          }
        ]
      }
    }
  },
  methods: {
    goAuth() {
      if(this.dataAuth.pass.length >= 3) {
        window.mp.trigger('cefS::auth:auth', JSON.stringify(this.dataAuth))
      }
    },
    goReg() {
      window.mp.trigger('cefS::auth:reg', JSON.stringify(this.dataReg))
    },
    goRecover() {
      window.mp.trigger('cefS::auth:recoverPass', this.dataLoginPass)
    },
    goChoiceSpawn(name) {

      this.characters.characters.forEach((el) => {
        if(el.name === name) {
          this.dataChoiceSpawn = el
          this.activeChoiceSpawn = true
          return 0
        }
      })

    }
  },
}
</script>

<style src="../../assets/css/Auth/auth.css" scoped>

</style>