  },
  methods: {
    changeUrl(name) {
      this.$router.push({name})
    }
  },
  mounted() {
    document.addEventListener("keydown", e => {
      if(e.keyCode === 77 && this.$route.name === 'empty') {
        this.$router.push({name: 'phone'})
      } else if (e.keyCode === 77 && this.$route.name === 'phone') {
        this.$router.push({name: 'empty'})
      }
    })
  }
