<template>

  <div class="ads">

    <div class="_title">
      <h1>Объявления</h1>
    </div>

    <div class="ads__title">
      <h1><span>WEAZZLE</span> NEWS</h1>
      <p>$5 за символ.</p>
    </div>

    <div class="ads__text">

      <textarea v-model="text" maxlength="500" cols="30" rows="10"></textarea>

    </div>

    <div class="ads__submit _flex _center">
      <div :class="{active: text}"
           @click="send"
           class="submit _flex _center">
        <p>Подать объявление</p>
      </div>
    </div>

  </div>

</template>

<script>
export default {
  name: "ads",
  data() {
    return {
      text: ''
    }
  },
  methods: {
    send() {
      window.mp.trigger('cefS::phone:ads', this.text)
      this.$emit('userSend')
    }
  },
}
</script>

<style lang="scss" src="../../../assets/scss/Phone/ads.scss" scoped>

</style>