<template>

  <div class="block">

    <div class="content-message">
      <div class="message-info">
        <div class="info-icon">
          <img src="../../../assets/img/ChoiseCharacter/DonateSlot/info.png" alt="">
        </div>
        <div class="info-title">
          <div class="message-name">
            <p>Тревор</p>
          </div>
          <p>На твоем счету {{donate}} vcoins используй их с умом парень</p>
        </div>
      </div>
    </div>
    <div class="block-content">
      <choiseCard v-for="num in allCharacters"
                  :characterData="characters[num] === null ? {'no': true} : characters[num]"
                  :num="num"
                  :donate="donate"
                  :key="num" @choiceCharacter="choiceCharacter"
                  @startCreateCharacter="returnCreateCharacter"
      />
    </div>
    <div class="block-message">

    </div>
  </div>

</template>

<script>

import choiseCard from "./choiseCard";

export default {
  name: 'choiseCharacter',
  components: {choiseCard},
  props: {
    characters: Array,
    donate: Number
  },
  data() {
    return {
      allCharacters: [0, 1, 2]
    }
  },
  methods: {
    choiceCharacter(name) {
      this.$emit('goChoiceSpawn', name)
    },
    returnCreateCharacter() {
      this.$emit('createCharacter')
    }
  },
}
</script>

<style src="../../../assets/scss/choiseCharacter/choiseCharacter.scss" lang="scss" scoped>



</style>