<script>
export default {
  name: "createCharacter",
  data() {
    return {
      valRotate: 0,
      valHeight: 0,
      valRange: 0,
      urlMenu: 'parents',
      parentsFather: ['John Dean', 'Chase Parker'],
      parentsMother: ['Cris Water', 'Mother Name'],
      activeFather: 0,
      activeMother: 0,
      dataCreate: {
        sex: 0,
        skinColor: 50,
        similarity: 50,
        father: '',
        mother: ''
      }
    }
  },
  methods: {
    changeParent(arrow, parent) {

      console.log(arrow)

      if (arrow === 0 ) {
        if(parent === 0) {
          if(this.activeFather === 0) {
            this.activeFather = this.parentsFather.length - 1
            return 0;
          }
          this.activeFather -= 1
          return 0;
        }
        if(this.activeMother === 0) {
          this.activeMother = this.parentsMother.length - 1
          return 0;
        }
        this.activeMother -= 1
        return 0;
      }

      if(parent === 0) {
        if(this.activeFather === this.parentsFather.length - 1) {
          this.activeFather = 0
          return 0;
        }
        this.activeFather += 1
        return 0;
      }
      if(this.activeMother === this.parentsMother.length - 1) {
        this.activeMother = 0
        return 0;
      }
      this.activeMother += 1
      return 0;
    }
  },
}
</script>

