<template>
  <div class="block" :class="{active: url != 'Main'}">
    <div class="block-content">
      <div class="content-header">
        <div class="header-time">
          <p>{{ dateData.time }}</p>
        </div>
        <div class="header-icons">
          <div class="icons-network">
            <img src="../../assets/img/Phone/network.png">
          </div>
          <div class="icons-wifi">
            <img src="../../assets/img/Phone/wifi.png">
          </div>
          <div class="icons-btr">
            <img src="../../assets/img/Phone/btr.png">
          </div>
        </div>
      </div>
      <div class="content-main">
        <Main @returnUrl="activeBack" v-show="url === 'Main'" :dateData="dateData" :startPhone="startPhone" />
        <Contacts @callNumberUser="callNumber"
                  :contactsList="contacts"
                  v-show="url === 'Contacts'" />
        <Call ref="callWindow"
              @removeCall="removeCall"
              :dataCallFromUser="dataCallFromUser"
              :dataCallToUser="dataCallToUser"
              v-show="url === 'Call'" > </Call>
        <setNumber @callNumberUser="callNumber"
                   @newContact="addContact"
                   v-show="url === 'setNumber'" />
        <parking ref="parking" v-show="url === 'parking'" />
        <navigator v-show="url === 'navigator'" />
        <ads @userSend="activeBack" v-show="url === 'ads'" />
      </div>
      <div class="content-back"
           :class="{active: back}">
        <div class="back-line" @click="url !== 'Call' ? activeBack('Main',true) : null">

        </div>
      </div>
    </div>
  </div>
</template>

<script>

import Main from "./components/Main";
import Contacts from "./components/Contacts";
import Call from "./components/Call";
import setNumber from "./components/setNumber";
import parking from "./components/parking";
import navigator from "./components/navigator";
import ads from "./components/ads";

export default {
  name: "Phone",
  components: {
    Main,
    Contacts,
    Call,
    setNumber,
    parking,
    navigator,
    ads
  },
  data() {
    return {
      url: 'Main',
      dateData: {
        time: '12:14',
        day: '1',
        mounth: 'Январь'
      },
      back: false,
      startPhone: true,
      dataCallFromUser: {
        active: false,
        name: 'Неизвестно',
        number: 1124124
      },
      dataCallToUser: {
        name: 'John Dean',
        number: 12345,
        active: false
      },
      contacts: [
        {
          name: 'Chase Parker',
          number: 123456789
        }
      ]
    }
  },
  methods: {
    updateDate() {
      const nowDate = new Date();
      this.dateData.time = nowDate.getHours() + ":" + (nowDate.getMinutes().toString().length > 1 ? nowDate.getMinutes() : "0" + nowDate.getMinutes());
      this.dateData.day = nowDate.getDate();
      switch (nowDate.getMonth()){
        case 1: this.dateData.mounth = 'Января'; break;
        case 2: this.dateData.mounth = 'Февраля'; break;
        case 3: this.dateData.mounth = 'Марта'; break;
        case 4: this.dateData.mounth = 'Апреля'; break;
        case 5: this.dateData.mounth = 'Мая'; break;
        case 6: this.dateData.mounth = 'Июня'; break;
        case 7: this.dateData.mounth = 'Июля'; break;
        case 8: this.dateData.mounth = 'Августа'; break;
        case 9: this.dateData.mounth = 'Сентября'; break;
        case 10: this.dateData.mounth = 'Октября'; break;
        case 11: this.dateData.mounth = 'Ноября'; break;
        case 12: this.dateData.mounth = 'Декабря'; break;

      }
    },
    activeBack(url = 'Main', click = false) {

      if(click && this.url === 'Main') return;

      if(this.back && !this.startPhone) return;

      this.back = true

      if(!this.startPhone) {
        setTimeout(() => {
          this.url = ''
        }, 1500)
      }

      setTimeout(() => {
        this.back = false

        this.url = url
      }, 1600)
    },
    // eslint-disable-next-line no-unused-vars
    callNumber(data) {
      this.dataCallToUser.active = false
      this.dataCallFromUser = data
      console.log(this.dataCallFromUser)
      this.dataCallFromUser.active = true
      window.mp.trigger('cefS::phone:callFromUser', JSON.stringify(this.dataCallFromUser))
      this.activeBack('Call')
    },
    callToUser(data) {
      if(this.url === 'Call') {
        window.mp.trigger('cefS::phone:removeCall', data)
        return
      }
      this.dataCallToUser = data
      this.dataCallToUser.active = true
      this.activeBack('Call')
    },
    removeCall() {
      this.activeBack();
      window.mp.trigger('cefS::phone:removeCall', this.dataCallToUser.active ?
                                                  JSON.stringify(this.dataCallToUser) :
                                                  JSON.stringify(this.dataCallFromUser))
      setTimeout(() => {
        this.dataCallToUser.active = false
      }, 2000)
    },
    test() {
      this.callToUser({
        name: 'Check',
        number: 9436346
      })
    },
    addContact(number) {
      window.mp.trigger('cefS::phone:addContact', number)
    }
  },
  mounted() {
    this.updateDate()
    setInterval(this.updateDate, 15000)

    setTimeout(() => {
      this.activeBack();
      setTimeout(() => {
        this.startPhone = false
      }, 1500)
    }, 800)
  }
}
</script>

<style lang="scss" src="../../assets/scss/Phone/Phone.scss" scoped>

.block {
  & h1 {
    color: rebeccapurple;
  }
}

</style>