<template>
  <div v-if="!activeWindow" class="loader">
    <div class="loader-title">
      <h1>Добро пожаловать</h1>
      <p>Информация загружается</p>
    </div>
    <div class="loader-gif">
      <img src="../../../assets/img/loader.gif">
    </div>
  </div>
  <div class="block">
    <div class="block-container">
      <div class="container-title">
        <div class="title-exit">
          <div class="exit-btn">
            <p>E</p>
          </div>
          <div class="exit-text">
            <p>Чтобы выйти</p>
          </div>
        </div>
        <div class="title-mouse">
          <img src="../../../assets/img/Shops/mouse.png">
          <p>ЛКМ - Осмотреть</p>
        </div>
      </div>
      <div v-if="active" class="container-content">
        <div class="content-discr" v-if="active">
          <div class="discr-title">
            <h1>{{active.name}}</h1>
          </div>
          <div class="discr-content">
            <div class="content-object">
              <div class="object-child">
                <div class="object-title">
                  <p>Стиль</p>
                </div>
                <div class="choice-object">
                  <div class="choice-btn"
                       @click="styleNum === 0 ?
                       (styleNum = active.objects.length-1, changePreset(styleNum)) :
                       (styleNum -= 1, changePreset(styleNum))">
                    <p>&lt;</p>
                  </div>
                  <div class="choice-input input">
                    <p>{{active.objects[styleNum].name}}</p>
                  </div>
                  <div class="choice-btn"
                       @click="styleNum === active.objects.length-1 ?
                       (styleNum = 0, changePreset(styleNum)) : (styleNum += 1, changePreset(styleNum))">
                    <p>></p>
                  </div>
                </div>
              </div>
            </div>
            <div class="content-price">
              <p>Цена</p>
              <p class="price">${{active.objects[styleNum].price}}</p>
            </div>
            <div class="content-btns">
              <div class="btns-btn" @click="buyShop('cash')"><p>Оплатить картой</p></div>
              <div class="btns-btn"
                   @click="buyShop('bank')"
                   style="background: linear-gradient(90deg, #FFFFFF 0%, #FFFFFF 100%);"><p>Оплатить наличными</p></div>
            </div>
          </div>
        </div>
        <div class="content-list">
          <div class="list-title">
            <h1>{{name}}</h1>
          </div>
          <div class="list-content">
            <div class="content-item"
                 v-for="(obj, i) in menu"
                 @click="active = data[i]; styleNum = 0; changePreset(0)"
                 :key="obj.name" :class="{active: active.name === obj.name}">
              <img :src="obj.img" alt="">
              <p>{{obj.name}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Tatoo",
  data() {
    return {
      activeWindow: false,
      name: 'Тату салон',
      data: [
        {
          name: 'Голова',
          objects: [
            {
              name: 'B1',
              price: 2000,
            },
            {
              name: 'B2',
              price: 5000,
            }
          ]
        },
        {
          name: 'Шея',
          objects: [
            {
              name: 'B1',
              price: 2000,
            },
            {
              name: 'B2',
              price: 5000,
            }
          ]
        },
        {
          name: 'Туловище',
          objects: [
            {
              name: 'B1',
              price: 2000,
            },
            {
              name: 'B2',
              price: 5000,
            }
          ]
        },
        {
          name: 'Левая рука',
          objects: [
            {
              name: 'B1',
              price: 2000,
            },
            {
              name: 'B2',
              price: 5000,
            }
          ]
        },
        {
          name: 'Правая рука',
          objects: [
            {
              name: 'B1',
              price: 2000,
            },
            {
              name: 'B2',
              price: 5000,
            }
          ]
        },
        {
          name: 'Левая нога',
          objects: [
            {
              name: 'B1',
              price: 2000,
            },
            {
              name: 'B2',
              price: 5000,
            }
          ]
        },
        {
          name: 'Правая нога',
          objects: [
            {
              name: 'B1',
              price: 2000,
            },
            {
              name: 'B2',
              price: 5000,
            }
          ]
        }
      ],
      active: {
        name: 'Голова',
        objects: [
          {
            name: 'B1',
            price: 2000,
          }
        ]
      },
      styleNum: 0,
      menu: [
        {
          name: 'Голова',
          img: require('../../../assets/img/Shops/Tatoo/head.png')
        },
        {
          name: 'Шея',
          img: require('../../../assets/img/Shops/Tatoo/neck.png')
        },
        {
          name: 'Туловище',
          img: require('../../../assets/img/Shops/Tatoo/torso.png')
        },
        {
          name: 'Левая рука',
          img: require('../../../assets/img/Shops/Tatoo/strong-left.png')
        },
        {
          name: 'Правая рука',
          img: require('../../../assets/img/Shops/Tatoo/strong-right.png')
        },
        {
          name: 'Левая нога',
          img: require('../../../assets/img/Shops/Tatoo/leg-left.png')
        },
        {
          name: 'Правая нога',
          img: require('../../../assets/img/Shops/Tatoo/leg-right.png')
        }
      ]
    }
  },
  methods: {
    setInfo(data, name) {
      this.objects = data
      this.name = name
      this.activeMenu = this.data[0]
      this.activeWindow = true
    },
    changePreset(num) {
      window.mp.trigger('cefS::tatoo:changePreset', JSON.stringify({
        place: this.active.name,
        num: num
      }))
    },
    buyShop(pay) {
      window.mp.trigger('cefS::tatoo:buyPreset', JSON.stringify({
        place: this.active.name,
        num: this.styleNum,
        price: this.active.objects[this.styleNum].price,
        type: pay
      }))
    }
  },
  mounted() {
    this.active = this.data[0]
    console.log(this.active)
  }
}
</script>

<style lang="scss" src="../../../assets/scss/Shops/Shop.scss" scoped>

</style>