import axios from "axios";
import {url} from "../config.js"

class bankController {

    static async getStarted(uuid) {
        try {
            return axios({
                method: "post",
                url: url + "default/getMoney",
                data: {
                    uuid
                }
            });
        } catch (e) {
            return 400
        }
    }

    static async withdrawMoney(data) {
        try {
            return axios
                .post(url + 'bank/withdrawMoney', {
                    ...data
                })
        } catch (e) {
            return 400
        }
    }
}

export default bankController