<template>

  <div class="block"
       ref="blockWindow">
    <div class="block-content">

      <div class="content-title">
        <h1>Отделение банка "<span>MAZE</span>BANK"</h1>
        <p>Отделение работает круглосуточно и без выходных.</p>
      </div>

      <div class="content-cards _flex">
        <div class="cards-item _flex first">
          <div class="item-discr" style="white-space: nowrap">
            <h1>Баланс</h1>
            <p style="margin-bottom: 10%; color: #5AFF75;">${{data.bank}}</p>
            <h1>Номер счета</h1>
            <p style="color: #5AFF75;">669669</p>
          </div>
          <div class="item-obj">
            <div class="obj-img">
              <img src="../../assets/img/bank/balance.png" alt="">
            </div>
          </div>
        </div>
        <div class="cards-item _flex">
          <div class="item-discr">
            <h1>Вывести средства</h1>
            <p>Вывод без задержки.</p>
          </div>
          <div class="item-obj">
            <div class="obj-img">
              <img src="../../assets/img/bank/payment.png" alt="">
            </div>
            <div @click="activeModal(0)" class="obj-submit _flex _center">
              <p>Перейти к операции</p>
            </div>
          </div>
        </div>
        <div class="cards-item _flex">
          <div class="item-discr">
            <h1>Внести залог за дом</h1>
            <p>Без комиссий и переплат.</p>
          </div>
          <div class="item-obj">
            <div class="obj-img">
              <img src="../../assets/img/bank/house.png" alt="">
            </div>
            <div @click="activeModal(1)" class="obj-submit _flex _center">
              <p>Перейти к операции</p>
            </div>
          </div>
        </div>
        <div class="cards-item _flex">
          <div class="item-discr">
            <h1>Внести залог за бизнес</h1>
            <p>Без комиссий и переплат.</p>
          </div>
          <div class="item-obj">
            <div class="obj-img">
              <img src="../../assets/img/bank/business.png" alt="">
            </div>
            <div @click="activeModal(2)" class="obj-submit _flex _center">
              <p>Перейти к операции</p>
            </div>
          </div>
        </div>
        <div class="cards-item _flex">
          <div class="item-discr">
            <h1>Перевести на другой счёт</h1>
            <p>Без комиссий и переплат.</p>
          </div>
          <div class="item-obj">
            <div class="obj-img">
              <img src="../../assets/img/bank/account.png" alt="">
            </div>
            <div @click="activeModal(3)" class="obj-submit _flex _center">
              <p>Перейти к операции</p>
            </div>
          </div>
        </div>
        <div class="cards-item _flex last">
          <div class="item-discr">
            <h1>Внести средства</h1>
            <p>Поступление без задержек.</p>
          </div>
          <div class="item-obj">
            <div class="obj-img">
              <img src="../../assets/img/bank/card.png" alt="">
            </div>
            <div @click="activeModal(4)" class="obj-submit _flex _center">
              <p>Перейти к операции</p>
            </div>
          </div>
        </div>
      </div>

      <div class="moneys">
        <div style="width: 100%; height: 100%; position:relative;">
          <div class="money__left_down">
            <img src="../../assets/img/bank/moneyLeftDown.png" alt="">
          </div>
          <div class="money__left_up">
            <img src="../../assets/img/bank/moneyLeftUp.png" alt="">
          </div>
          <div class="money__center">
            <img src="../../assets/img/bank/moneyCenter.png" alt="">
          </div>
          <div class="money__right">
            <img src="../../assets/img/bank/moneyRight.png" alt="">
          </div>
        </div>
      </div>
    </div>
    <div class="block-modal"
         ref="modalWindow">
      <div style="z-index: 99;" class="modal-content">
        <div class="content-title">
          <h1>{{modal.type || 'Перевод средств по счету'}}</h1>
        </div>
        <div class="content-window">
          <div class="window-input" v-if="!modal.type">
            <p>Укажите номер счета | {{modal.num.length}}/20</p>
            <input v-model="modal.num"
                   maxlength="20"
                   type="text">
          </div>
          <div class="window-input">
            <p>Укажите сумму</p>
            <input @input="test"
                   maxlength="12"
                   v-model="modal.sum"
                   type="text">
          </div>
        </div>
        <div class="content-submit _flex _center">
          <div class="submit _flex"
               :class="{OnSubmit: activeModal, CreateQuery: modal.type ? modal.sum : modal.sum && modal.num}"
               @click="!modal.type ?
                      (modal.sum && modal.num ? goSumToUser() : null) :
                      (modal.sum ? goSum() : null)">
            <p>Готово</p>
          </div>
        </div>
        <div class="modal-content__success" ref="modalSuccess"></div>
      </div>
    </div>
    <loader v-if="!active" />
  </div>

</template>

<script>

import bankController from "../../apiControllers/bankController";
import loader from "../loader";

export default {
  name: "bank",
  components: {
    loader
  },
  props: {
    uuid: String
  },
  data() {
    return {
      active: false,
      modal: {
        num: '',
        sum: '',
        type: ''
      },
      data: {
        money: Number,
        bank: Number
      },
      activeSubmit: false
    }
  },
  methods: {
    test() {
      this.modal.sum = this.modal.sum.replace(/\D/g, '')
    },
    activeModal(num) {
      switch (num) {
        case 0: {
          this.modal.type = 'Вывод средств'
          break
        }
        case 1: {
          this.modal.type = 'Внесение залога за дом'
          break
        }
        case 2: {
          this.modal.type = 'Внесение залога за бизнес'
          break
        }
        case 3: {
          this.modal.type = ''
          break
        }
        case 4: {
          this.modal.type = 'Внесение средств'
        }
      }
      console.log(this.modal.type)
      this.$refs.modalWindow.classList.add('active')
    },
    goSumToUser() {
      window.mp.trigger('cefS::bank:sendMoneyOnAccount', JSON.stringify(this.modal))
      this.$refs.modalWindow.classList.remove('active')
    },
    async goSum() {
      switch (this.modal.type) {
        case "Вывод средств": {

          if(this.modal.sum <= this.data.bank) {
            this.activeSubmit = true
          } else {
            return
          }

          this.data.bank = this.data.bank - Number(this.modal.sum)
          this.data.money = this.data.money + Number(this.modal.sum)

          window.mp.trigger('cefS::bank:transferToMoney', this.modal.sum)

          this.$refs.modalSuccess.classList.add('modal-content__success_active')
          break
        }
        case "Внесение залога за дом": {
          window.mp.trigger('cefS::bank:moneyOnHouse', this.modal.sum)
          break
        }
        case "Внесение залога за бизнес": {
          window.mp.trigger('cefS::bank:moneyOnBusiness', this.modal.sum)
          break
        }
        case "Внесение средств": {
          window.mp.trigger('cefS::bank:depositMoney', this.modal.sum)
          break
        }
      }
      setTimeout(() => {
        this.modal.sum = ''
        this.modal.num = ''
        this.$refs.modalWindow.classList.remove('active')
        this.$refs.modalSuccess.classList.remove('modal-content__success_active')
        this.activeSubmit = false
      }, 1100)
    },
    async startedWork() {
      const res = await bankController.getStarted(this.$store.state.uuid)

      if(res.status === 400) {
        await this.$router.push({name: 'empty'})
      }

      this.data = res.data[0]
    }
  },
  async mounted() {

    document.querySelector('.block-modal').addEventListener("click", e => {
      if(e.target.classList.contains('block-modal')) {
        this.$refs.modalWindow.classList.remove('active')
        this.modal.sum = ''
        this.modal.num = ''
      }
    })

    await this.startedWork()

    this.active = true
  }
}
</script>

<style lang="scss" src="../../assets/scss/Bank/bank.scss" scoped>

</style>