<script>
export default {
  name: "sklad",
  data() {
    return {
      data: [

      ],
      activeModal: 0,
      sum: '',
      from: 0
    }
  },
  methods: {
    fromClient() {
      switch (this.activeModal) {
        case 1: {
          window.mp.trigger('cefS::sklad:gun', JSON.stringify({
            operation: this.from,
            model: 'Моделька оружия'
          }))
          break
        }
        case 2: {
          window.mp.trigger('cefS::sklad:material', JSON.stringify({
            operation: this.from,
            model: 'Моделька оружия',
            count: 0
          }))
          break
        }
        case 3: {
          window.mp.trigger('cefS::sklad:money', JSON.stringify({
            operation: this.from,
            count: 0
          }))
          break
        }
        case 4: {
          window.mp.trigger('cefS::sklad:heal', JSON.stringify({
            operation: this.from,
            count: 0
          }))
          break
        }
        case 5: {
          window.mp.trigger('cefS::sklad:narko', JSON.stringify({
            operation: this.from,
            count: 0
          }))
          break
        }
      }
    }
  },
  mounted() {
    const modals = document.querySelectorAll('.modal')
    const containers = document.querySelectorAll('.modal-container')

    modals.forEach(e => {
      e.addEventListener("click", () => {
        this.activeModal = 0
      })
    })
    containers.forEach(e => {
      e.addEventListener("click", e => {
        e.stopImmediatePropagation()
      })
    })
  }
}
</script>

